h1 {
  color: #1fb6ff;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

body {
  color: gray;
}
header, nav, aside,  footer, section, article {display: block;} 

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: gray;
}

nav ul {
  float: left;
}

nav li {
  margin: 0 15px;
}

aside ul li{
  margin: 0 15px; 
}
.container {
  display: grid;
  grid-template-rows: 80px 50px 1fr 50px;
}

header, nav, aside,  footer {
  border: 1px gray solid;
  margin: 0px;
}


footer {
  background-color: #364657;
  color: lightgray;
  font-size: 1em;
  padding-top: 0.5em;
  padding-left: 2em;
  height: 2.2em;
}

aside {
  display: inline-block;
  float: left;
  width: 20%;
}

main {
  margin: 5px;
  vertical-align: top;
  display: inline-block;
  float: left;
}

article {
  display: block;
  float: left;
  margin: 10px;
  width: 70%;
}

.home-content { 
  text-align: center;
}

header {
  background-color:#ffffff;
  border-bottom: 3px #b4b4b4 solid;
  text-align: center;
}


nav {
  display: block;
  float: left;
  text-align: right;
  vertical-align: middle;
  background-color: #e5e6e9;
}

nav li {
  display: inline-block;
}

.top-contact-container { 
  
  line-height: 0.5;
  vertical-align: middle;
}

.logo-container {
  vertical-align: middle;
}


.user ul {
  margin: 0px;
  padding: 0px;
}

.user input {
  font-size: 1em;
  margin: 0px 5px;
}

.user li {
  display: inline-block;
  float: none;
  margin-top: 10px;  
}

.user li button {
  border: 1px blue solid;
  padding: 5px 15px;
  background-color: #1fb6ff;
  color: white;
  border-radius: 5px;
}

.user span {
  margin: 15px;
}

.user .message {
  display: inline-block;
  width: 250px;
  margin: 15px 20px;
  text-align: left;
}